body{
  text-align: center;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  font-family: sans-serif;
  align-items: center;
}
h1{
  text-align: left;
  font-size: 22px;
  padding: 16.1px 0 ;
  /*margin: 19px 0px;*/
}
.main{
  width: 90%;
  margin: 0 auto;
  padding: 0;
  height: 100%;
}

.left_line{
  padding: 25px 25px;
  text-align: left;
  margin: 25px 25px;
}

.left_side, .middel_side, .right_side{
  height: 600px;
  width: 100%;
  padding: 0;
}

.pic{
  height:  600px;
  width: 100%;
}


.form{
  width: 100%;
   height: 50px;
   margin: 40px 0 40px
}
.form input {
  color: #595f6e;
  padding-top: 20px;
  padding-left: 10px;
  border: none;
  border-bottom: 1px solid #595f6e;
  outline: none;
}


  .btn{
    margin-top: 70px;
    border: none;
    padding: 10px 35px;
  }
  .font{
    color: #595f6e;
  }
.right_title{
  color: red;
}
.car{
  max-width: 80px;
}
.right_line{
  text-align: right;
}
@media (max-width : 1020px) {
    img.pic {
    display: none;
  }
  #a,#back, #c { display: none; }
  .left_line{
    text-align: center;
  }
  .right_line{
    text-align: center;
  }
  h1{
    text-align: center;
  }

}
